
.img-container {
    background-image: url(../../assets/val\ thores3.jpg);
    /* background-image: url(../../assets/home\ background.jpg); */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: left;
    width: 100%;
    height: 25rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.description{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.description h1 {
    margin: 2rem 1rem;
    font-size: 1rem;
}

.view-wrapper {
    margin-bottom: 3rem;
}

.view {
    width: 100%;
    height: auto;
}

.description h2 {
    font-size: 1rem;
    margin: 0 1rem 2rem;

}

@media screen and (min-width: 768px) {
    .img-container {
        height: 35rem;
    }

    .view-wrapper {
        width: 95%;
        display: flex;
        justify-content: center;
        align-items: center;
        
    }

    .description h2 {
        width: 30%;
        margin: 2rem 2rem 2rem 0;
    }

    .view {
        width: 70%;
        height: auto;
    }
}